.tabArea-root{position:relative}.tabArea-root .tab{height:16px;padding:5px;display:inline-block;font-family:Arial;font-size:14px;color:grey;border:1px solid lightgrey;border-top:none;border-radius:0 0 10px 10px;float:right;cursor:pointer}.tabArea-root .tab.selected{font-weight:bold;background-color:white}.tabArea-root .tab-select-line{width:100%;border-bottom:3px solid darkcyan;padding-top:2px}.tabArea-root .tab-content{height:calc(100% - 27px);border-bottom:1px solid lightgrey}.tabArea-root .tabs{height:27px;position:absolute;bottom:0;right:0;width:100%}



